:root {
  --color-primary-dark: 17 24 39;
  --color-primary-light: 75 81 96;
  --color-white: 255 255 255;
  --color-highlight: 255 26 117;

  --header-position: sticky;
  --content-offset: 116px;
  --header-height: 180px;
  --header-mb: -116px;

  --avatar-image-transform: translate3d(0rem, 0, 0) scale(1);
  --avatar-border-transform: translate3d(-0.2222222222222222rem, 0, 0) scale(1.7777777777777777);
  --avatar-border-opacity: 0;

  --header-top: 0px;
  --avatar-top: 0px;
}

html {
  font-family: "Work Sans",
  sans-serif,
  Apple Color Emoji,
  Segoe UI Emoji,
  Segoe UI Symbol,
  Noto Color Emoji;

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    font-family: "Wix Madefor Display", serif;
  }

  .prose :where(pre):not(:where([class~=not-prose],[class~=not-prose] *)) {
    border-radius: 0.75rem;
    padding: 24px 32px 24px 32px;
    width: 60vw;
    position: relative;
    left: -33.3333333%;
  }

  code, pre, kbd, samp {
    font-family: 'Zed Mono Web', ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, Liberation Mono, Courier New, monospace !important;
    font-size: 16px !important;
    font-style: oblique;
  }

  body {
    a {
      transition: color 0.25s ease-out,
      background-color 0.25s ease-out,
      border-color 0.25s ease-out,
      box-shadow 0.25s ease-out;
    }

    p {
      font-size: 18px;
    }

    .page, .post {

      h2 {
        font-size: 28px;
        color: rgb(var(--color-white));
      }

      h3 {
        font-size: 22px;
      }

      a {
        color: rgb(var(--color-white));
        border-bottom: 1px solid rgb(var(--color-white));
        transition: color .25s ease-out, background-color .25s ease-out, border-color .25s ease-out, box-shadow .25s ease-out;

        &:hover {
          color: rgb(var(--color-highlight));
          border-bottom: 1px solid rgb(var(--color-highlight));
        }
      }

      .kg-gallery-card {
        left: -80%;

        figcaption {
          display: none;
        }
      }
    }


    .kg-gallery {

      &-card {
        width: 100vw;
        left: -90%;
        position: relative;
      }

      &-container {
        display: flex;
        overflow: visible;
        gap: 1.5rem;
        justify-content: center;
        min-height: 300px;

        .kg-gallery-row {
          display: flex;
          overflow: visible;
          gap: 0.5rem;
          justify-content: center;

          &:not(:first-of-type) {
            margin: 0 !important;
          }

          .kg-gallery-image {
            position: relative;
            aspect-ratio: 9/10;
            width: 18rem;
            flex: none !important;
            overflow: hidden;
            border-radius: 1rem;

            img {
              position: absolute;
              width: 100%;
              height: 100%;
              inset: 0;
              object-fit: cover;
              color: transparent;
            }
          }
        }
      }
    }

    figure {
      display: flex;
      flex-direction: column;

      &.kg-card {
        img {
          margin-top: 0 !important;
          margin-bottom: 0 !important;
          border-radius: 12px;
        }
      }

      img {
        object-fit: cover;
        width: auto;
        height: auto;
        border-top-left-radius: 4px;
        border-top-right-radius: 4px;
      }

      figcaption {
        padding: 4px 8px;
        font-family: "Work Sans", serif;
        font-size: 14px;
        font-style: italic;
        color: rgb(var(--color-white) / 40%);
        border-bottom-left-radius: 4px;
        border-bottom-right-radius: 4px;
        background: rgb(var(--color-primary-dark) / 20%);

        p {
          font-size: 14px;
          text-align: center;
        }
      }
    }
  }
}

.kg-width {
  &-wide img {
    max-width: 85vw;
  }

  &-full img {
    max-width: 100vw;
  }
}
